import React,{Component,Fragment} from 'react'
import './static/App.css'
import Page from "./Page";
import MyWeb3 from './GameWeb3'
import {
    BrowserRouter as Router,
    Route,
    NavLink
  } from "react-router-dom"

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
             AdminArea:()=>{return(<Fragment></Fragment>)},
             WalletBtn: ()=>{return(<div></div>)},
             result: null,
        }
    }
    
    componentDidMount(){
        let ethereum = window.ethereum
        let that = this
        if (typeof  ethereum !== 'undefined' || (typeof window.web3 !== 'undefined')) {
            MyWeb3.init().then(function(res){
                that.getPrice()
            })
            ethereum.on('accountsChanged', function (accounts) {
                console.log("accountsChanged:"+accounts)
                if (!accounts || !accounts.length) {
                    that.setState({WalletBtn : () =>{
                        return(
                        <div className="wallet-btn" onClick={that.wallet.bind(this)}>
                            <span>
                            请连接钱包!
                            </span>
                        </div>)
                        }
                    })
                } else {
                    that.getPrice()
                }
                
                //window.location.reload()
            })
            ethereum.on('chainChanged', function (chainId) {
                console.log("chainChanged:"+chainId)
                //window.location.reload()
            })
            ethereum.on('networkChanged', function (networkVersion) {
                console.log("networkChanged:"+networkVersion)
                //window.location.reload()
            })
        }else {
            alert('你必须安装MetaMask!')
        }
        
    }

    // 连接钱包
    wallet() {
        let that = this
        MyWeb3.init().then(function(res){
            // that.getPrice()
        })
    }

    // 获取eth余额
    getPrice() {
        let that = this
        MyWeb3.getBlance().then(function(result){
            that.setState({
                result: result
            })
            that.getId()
        })
        
        
    }

    getId() {
        let that = this
        MyWeb3.getId().then(function(result){
            that.setState({WalletBtn : () =>{
                return(
                <div className="wallet-accounts">
                    <span>
                        {result}
                    </span>
                    <span>
                        {that.state.result} ETH
                    </span>
                    <span className="accounts-span">
                        {window.defaultAccount}
                    </span>
                </div>)
            }})
        })
    }   

    btnFun() {
       window.location.href = window.location.origin + window.location.pathname + '#/home'
    }
    
    render() { 
        let AdminArea = this.state.AdminArea
        let WalletBtn = this.state.WalletBtn
        return (
            <Fragment>
                <Router basename='/bigrichman.github.io/#'>
                    <section className="zombies-hero no-webp app-block-intro">
                        <div className="container">
                            <div className="menu">
                                <div className="logo" onClick={this.btnFun.bind(this)}>

                                </div>
                                <ul>
                                    <li>
                                        <button className="start-course-btn">
                                            <span>
                                            <NavLink exact
                                             activeClassName="active"
                                              to="/home">主页</NavLink>
                                            </span>
                                        </button>
                                    </li>
                                    {/* <li>
                                        <button className="start-course-btn">
                                            <span><NavLink exact activeClassName="active" to="/MyZombie">我的收益</NavLink></span>
                                        </button>
                                    </li> */}
                                    {/* <li>
                                        <button className="start-course-btn">
                                            <span><NavLink to="/ZombieMarket">僵尸市场</NavLink></span>
                                        </button>
                                    </li>
                                    <li>
                                        <button className="start-course-btn">
                                            <span><NavLink to="/ZombieSimulator">基因模拟</NavLink></span>
                                        </button>
                                    </li> */}
                                    <AdminArea></AdminArea>
                                </ul>
                                <WalletBtn></WalletBtn>
                            </div>
                        </div>
                    </section>
                    <section className="zombie-container bg-walls no-webp">
                        <div className="container">
                            <div className="area">
                                <Route path="*" component={Page}></Route>
                            </div>
                        </div>
                    </section>
                    
                </Router>
            </Fragment>
            );
    }
}


export default App
