import React, { Component } from 'react'
import ZombieCard from "./ZombieCard";
import './static/ZombiePreview.css';
import MyWeb3 from './GameWeb3'
import {
    BrowserRouter as 
    Route,
    Link
  } from "react-router-dom"
import Page from "./Page";
import ComAlert from "./ComAlert";
import {fixInput} from "./common-method";
import homebg from "./static/homebg.jpg";
import jumpbg from "./static/jumpbg.jpg";

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
             grid:[],
             toArr: [],
             ToItem: ()=>{return(<div></div>)},
             flag: false,
             price: 0.005,
             blance: 0,
             error: false,
             // 降伞遮罩开关
             pending: false,
             // 降伞完成开关
             jumpConfimBg: false,
             // 选中地id
             toId: null,
             // 奖金池
             AllBonus: 0,
             NoteContent:()=>{
                return(
                    <div className="note">
                         <p>
                            基础操作说明：
                        </p>
                        <p>
                            1. 玩家需提前连接钱包并设置“跳伞”条件。
                        </p>
                        <p>
                            2. 跳伞时，用户的部分资金将被冻结。
                        </p>
                        <p>
                            3. “跳伞”后，玩家将随机降落在一块土地上。根据跳伞条件，自动完成土地购买或过路费交纳。
                        </p>
                        <p>
                            4.根据实际支付金额，退回剩余冻结资金。
                        </p>
                        <p>
                            5.若连续24小时没有玩家参与游戏，土地等级最高的前三名玩家将按照50%、30%、20%的比例瓜分奖金。
                        </p>
                    </div>
                    
                )
            },
        }
        this.inputPrice = this.inputPrice.bind(this)
    }
        
    componentDidMount(){
        let that = this
        let ethereum = window.ethereum
        if (typeof ethereum !== 'undefined' || (typeof window.web3 !== 'undefined')) {
            MyWeb3.init().then(function(res){
                that.zombieArmy()
                that.getPrice()
                that.getAllBonus()
            })
        }else {
            alert('You have to install MetaMask !')
        }
    }
    zombieArmy(){
        let that = this
        MyWeb3.getGridSize().then(function(result){
            if(result > 0){
                for(let i=0;i<result;i++){
                    MyWeb3.getGridInfo(i).then(function (result) {
                        let _grid = that.state.grid
                        result.gridId = i
                        _grid.push(result);
                        that.setState({grid:_grid})
                    })
                }
            }
        })
    }

    // 获取奖金池
    getAllBonus() {
        let that = this
        MyWeb3.poolTotalFee().then(function(res) {
            that.AllBonus = res
        })
    }

    // 获取eth余额
    getPrice() {
        let that = this
        MyWeb3.getBlance().then(function(result){
            that.setState({blance:result})
        })
    }

    inputPrice(event){
        if (this.state.error) {
            this.setState(
                {
                    error: false,
                }
            )
        }
        this.setState({
            price:fixInput(event.target.value,6)
        })
    }

    toEvent(data) {
        let adress = ''
        let that = this
            that.setState({flag:true})
            that.setState({ToItem : () =>{
                return(
                    <div>
                        <div className="item-note">
                        <p>
                            土地等级：{data['0']}
                        </p>
                        <p>
                            TOKEN地址：{data['1']}: {data['2']}
                        </p>
                        <p>
                            拥有者：{data['3']}
                        </p>
                    </div>
                    <div className="confim" onClick={that.confimEvent.bind(that)}>
                        确定
                    </div>
                </div>)
                }
            })
    }

    confimEvent() {
        this.setState({flag:false})
    }

    submitEvent() {
        if (this.state.price > this.state.blance) {
            this.setState(
                {
                    error: true,
                }
            )
        } else {
            this.confimEvent()
            this.confimJump()
            this.setState(
                {
                    pending: true,
                }
            )
        }
    }

    // 取消跳伞完成遮罩
    jumpBgEvent() {
        this.setState({
            jumpConfimBg: false,
            NoteContent: () => {
                return(
                    <div className="note">
                         <p>
                            基础操作说明：
                        </p>
                        <p>
                            1. 玩家需提前连接钱包并设置“跳伞”条件。
                        </p>
                        <p>
                            2. 跳伞时，用户的部分资金将被冻结。
                        </p>
                        <p>
                            3. “跳伞”后，玩家将随机降落在一块土地上。根据跳伞条件，自动完成土地购买或过路费交纳。
                        </p>
                        <p>
                            4.根据实际支付金额，退回剩余冻结资金。
                        </p>
                        <p>
                            5.若连续24小时没有玩家参与游戏，土地等级最高的前三名玩家将按照50%、30%、20%的比例瓜分奖金。
                        </p>
                    </div>
                    
                )
            },
        })
    }

    // 跳伞唤起钱包
    confimJump() {
        let that = this
        const price = this.state.price * Math.pow(10,18)
        const numbers = window.web3.utils.toWei(price.toString(),'wei')
        MyWeb3.fly(numbers).then(function(result) {
            if (result.type === 'receipt') {
                console.log(result)
            }
            // 返回类型
            let type = 1
            that.setState(
                {
                    pending: false,
                    jumpConfimBg: true,
                    toId: 4,
                    NoteContent:()=>{
                        if (type === 1) {
                                return(
                            <div className="note" style={{"zIndex":'30'}}>
                                 <p>
                                    感谢支持过路费🙏
                                </p>
                                <p>
                                    土地编号（NFT）：
                                </p>
                                <p>
                                        本次支付：
                                    </p>
                                    <p>
                                        解冻金额：
                                    </p>
                                    <p>
                                        奖池贡献：
                                    </p>
                            </div>
                            
                        )
                        } else {
                            return(
                                <div className="note" style={{"zIndex":'30'}}>
                                     <p>
                                        恭喜您购买一块新土地
                                    </p>
                                    <p>
                                       土地编号（NFT）：
                                    </p>
                                    <p>
                                        本次支付：
                                    </p>
                                    <p>
                                        解冻金额：
                                    </p>
                                    <p>
                                        奖池贡献：
                                    </p>
                                </div>
                                
                            )
                        }
                        
                    },
                }
            )
        }).catch(error => {
            if (error) {
                this.setState({
                    pending: false
                })
                return
            }
        })
    }

    jumpEvent() {
        this.setState({flag:true})
        this.setState({ToItem : () =>{
            return(
                <div className="jump">
                    <div className="jump-content">
                        <h4>
                            游戏设置
                        </h4>
                        <div className="form-blance">
                            土地价格
                            &nbsp;
                            <span className="current-box">
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor"
                                 strokeWidth="2" strokeLinecap="round"
                                 strokeLinejoin="round">
                                <circle cx="12" cy="12" r="10"></circle>
                                <path d="M9.09 9a3 3 0 0 1 5.83 1c0 2-3 3-3 3"></path>
                                <line x1="12" y1="17" x2="12.01" y2="17"></line></svg>
                                <div className="level-box">
                                    <h4>
                                        土地价格参照表
                                    </h4>
                                    <div className="level-pd ">
                                    <div className="level-content">
                                        <p>
                                            <span>
                                            土地等级
                                            </span>
                                            <span>
                                            土地价格(ETH)
                                            </span>
                                        </p>
                                        <p>
                                            <span>
                                            LV.1
                                            </span>
                                            <span>
                                            0.05
                                            </span>
                                        </p>
                                        <p>
                                            <span>
                                            LV.2
                                            </span>
                                            <span>
                                            0.055
                                            </span>
                                        </p>
                                        <p>
                                            <span>
                                            LV.3
                                            </span>
                                            <span>
                                            0.0605
                                            </span>
                                        </p>
                                        <p>
                                            <span>
                                            ......
                                            </span>
                                            <span>
                                            
                                            </span>
                                        </p>
                                        <p>
                                            <span>
                                            LV.50
                                            </span>
                                            <span>
                                            5.335947858
                                            </span>
                                        </p>
                                        <p>
                                            <span>
                                            ......
                                            </span>
                                            <span>
                                            
                                            </span>
                                        </p>
                                        <p>
                                            <span>
                                            LV.100
                                            </span>
                                            <span>
                                            626.391469992
                                            </span>
                                        </p>
                                    </div>
                                    </div>
                                    <div className="level-num">
                                        计算公式：Lv.N+1 = Lv.N * (1+10%)
                                    </div>
                                </div>
                            </span>
                            &nbsp;
                            不超过
                            <input
                             value={this.state.price}
                            className={this.state.error ? "form-input error-input" : "form-input"}
                             placeholder="0.005"
                            maxLength="79"
                            onChange={this.inputPrice}
                              />
                            ETH时，优先购买土地，否则支付过路费(0.005ETH/次)
                        </div>
                        <div className="form-accounts">
                            余额：{this.state.blance} ETH
                        </div>
                    </div>
                <div className="form-btn">
                    <div className="confim" onClick={this.confimEvent.bind(this)}>
                        取消
                    </div>
                    <div className="confim" onClick={this.submitEvent.bind(this)}>
                        确定
                    </div>
                </div>
            </div>)
            }
        })
    }

    componentWillUnmount() {
        this.setState = (state, callback) => {
            return
        }
    }
    render() { 
        let NoteContent = this.state.NoteContent
        let ToItem = this.state.ToItem
        let Flag = this.state.flag
        if(this.state.grid.length>0) {
            return ( 
                <div className="cards">
                    <img src={homebg}/>
                    <div className="img-box">
                        <div className="to-item-waper master-item">
                            <div className="to-item-befor">
                            </div>
                            <div className="to-item-last">
                                
                            </div>
                            <div className="bonus">
                                奖金池：
                                <span>
                                    {this.state.AllBonus}ETH
                                </span>
                            </div>
                        </div>
                        {this.state.grid.map((item,index)=>{
                            var name = item.name
                            const level = item['0']
                            const data = {...item}
                            return(
                                <div className="to-item-waper" key={item.gridId} style={{"zIndex": this.state.toId === item.gridId ? '30' : '1'}}>
                                    <div className="to-item-befor">
                                    </div>
                                    <div className="to-item-last" onClick={this.toEvent.bind(this,data)}>
                                        LV {level}
                                    </div>
                                </div>
                            )
                        })}
                        <div className="confim jumpConfim" onClick={this.jumpEvent.bind(this)}>
                            点我跳伞 🪂
                        </div>
                        <NoteContent></NoteContent>
                    </div>
                    <ComAlert flag={Flag}>
                        <ToItem></ToItem>
                    </ComAlert>
                    <div className="jump-bg" style={{'display': this.state.pending ? 'block' : 'none'}}>
                        <img src={jumpbg}/>
                    </div>
                    <div className="jump-confim-bg" onClick={this.jumpBgEvent.bind(this)} style={{'display': this.state.jumpConfimBg ? 'block' : 'none'}}>

                    </div>
                    <Route path="*" component={Page}></Route>
                </div> 
            )
        }else{
            return ( <div></div>)
        }
    }
}
 
export default Home;