import Web3 from "web3";
import abi from './GameControl.json'
// import ContractAddress from './ContractAddress'


const ContractAddress = {
    "4": "0x3C8026B0d8AB3fe2d9477133bc455Dbb4937ba70",
}

const NetworkList = {
    "1": "Ethereum",
    "2": "Morden",
    "3": "Ropsten",
    "4": "Rinkeby",
    "5": "Goerli",
    "42": "Kovan",
}

const GameWeb3 = {
    init() {
        /*
        '1': Ethereum Main Network
        '2': Morden Test network
        '3': Ropsten Test Network
        '4': Rinkeby Test Network
        '5': Goerli Test Network
        '42': Kovan Test Network
        */
        return new Promise((resolve, reject) => {
            //let currentChainId = parseInt(window.ethereum.chainId, 16)
            let ethereum = window.ethereum
            //禁止自动刷新，metamask要求写的
            ethereum.autoRefreshOnNetworkChange = false
            //开始调用metamask
            ethereum.enable().then(function (accounts) {
                //初始化provider
                let provider = window['ethereum'] || window.web3.currentProvider
                //初始化Web3
                window.web3 = new Web3(provider)
                //获取到当前以太坊网络id
                window.web3.eth.net.getId().then(function (result) {
                    let currentChainId = result
                    //设置最大监听器数量，否则出现warning
                    window.web3.currentProvider.setMaxListeners(300)
                    //从json获取到当前网络id下的合约地址
                    let currentContractAddress = ContractAddress[currentChainId]
                    if (currentContractAddress !== undefined) {
                        //实例化合约
                        window.MyContract = new window.web3.eth.Contract(abi.abi, currentContractAddress)
                        //获取到当前默认的以太坊地址
                        window.defaultAccount = accounts[0].toLowerCase()
                        //that.allEvents(window.MyContract)
                        resolve(true)
                    } else {
                        reject('Unknow Your ChainId:' + currentChainId)
                    }
                })
            }).catch(function (error) {
                console.log(error)
                if (error) {
                    window.location.reload()
                }
            })
        })
    },
    //格子总数
    getGridSize() {
        return new Promise((resolve, reject) => {
            window.MyContract.methods.getGridSize().call().then(function (gridSize) {
                resolve(gridSize)
            })
        })
    },
    //获得单个僵尸数据
    getGridInfo(index) {
        return new Promise((resolve, reject) => {
            if (index >= 0) {
                window.MyContract.methods.getGridInfo(index).call().then(function (gridInfo) {
                    resolve(gridInfo)
                })
            }
        })
    },



    //创建随机僵尸
    fly(_value) {
        return new Promise((resolve, reject) => {
            window.MyContract.methods.fly().send({from: window.defaultAccount, value: _value})
                .on('transactionHash', function (transactionHash) {
                    resolve({type:'transactionHash',transactionHash})
                })
                .on('confirmation', function (confirmationNumber, receipt) {
                    console.log({confirmationNumber: confirmationNumber, receipt: receipt})
                })
                .on('receipt', function (receipt) {
                    console.log({receipt: receipt})
                    //todo 这里有结果数据
                    // window.location.reload()
                    resolve({type:'receipt', receipt})
                })
                .on('error', function (error, receipt) {
                    console.log({error: error, receipt: receipt})
                    reject({error: error, receipt: receipt})
                })
        })
    },

    //奖金池
    poolTotalFee() {
        return new Promise((resolve, reject) => {
            window.MyContract.methods.poolTotalFee().call().then(function (gridSize) {
                resolve(gridSize)
            })
        })
    },

    //当前赛季
    currentVersion() {
        return new Promise((resolve, reject) => {
            window.MyContract.methods.currentVersion().call().then(function (gridSize) {
                resolve(gridSize)
            })
        })
    },

    // 获取eth余额
    getBlance() {
        return new Promise((resolve, reject) => {
            if (window.defaultAccount) {
                window.web3.eth.getBalance(window.defaultAccount).then(
                    function (result) {
                        resolve(window.web3.utils.fromWei(result, 'ether'))
                    });
            }
            
        })

    },

    // 获取当前网络id
    getId() {
        return new Promise((resolve, reject) => {
            window.web3.eth.net.getId().then(
                function (result) {
                    console.log('网络id:',result)
                    resolve(NetworkList[result])
                });
        })

    },

    //所有事件
    allEvents() {
        window.MyContract.events.allEvents({fromBlock: 0}, function (error, event) {
            console.log({allEvents: event})
        }).on("connected", function (subscriptionId) {
            console.log({connected_subscriptionId: subscriptionId})
        }).on('data', function (event) {
            console.log({event_data: event})
        }).on('changed', function (event) {
            console.log({event_changed: event})
        }).on('error', function (error, receipt) {
            console.log({event_error: error, receipt: receipt})
        })
    },
    
}

export default GameWeb3;